  pagination(dataApi) {
    var i, j, temparray, chunk = 5;
    var arrTotal = [];
    for (i = 0, j = dataApi.length; i < j; i += chunk) {
      temparray = dataApi.slice(i, i + chunk);
      arrTotal.push(temparray);
    }

    if (arrTotal.length == 0) {
      this.setState({
        hidden: false
      })
    } else {
      this.setState({
        hidden: true
      })
    }

    this.setState({ arrPagination: arrTotal, data: arrTotal[this.state.indexPage] });
  }

  getData = async () => {
    this.setState({ isLoading: true });
    const res = await axios({
      baseURL: Constants.BASE_URL,
      url: Constants.LIST_PLUGIN_ORDER,
      method: 'POST',
    });

    let val = res.data.data.result;

    for(let i = 0; i < val.length; i++){
      const name_sale = await axios({
        baseURL: Constants.BASE_URL,
        url: Constants.PLUGIN_GET_USER_BY_BODY,
        method: 'POST',
        data: {
          id: val[i].Sale_Id
        }
      });

      val[i].Sale = name_sale.data.data
    }

    this.pagination(val);
    this.setState({ dataApi: val, arrName: res.data.data.company, arrPackage: res.data.data.package, isLoading: false });
  }

  loadName(com, pac) {
    const { arrName, arrPackage } = this.state;
    var nameCom = arrName.findIndex(x => x._id === com);
    var namePac = arrPackage.findIndex(x => x._id === pac);
    return { com: arrName[nameCom].name, pac: arrPackage[namePac].name }
  }

  getDataBySale = async () => {
    this.setState({ isLoading: true });
    const res = await axios({
      baseURL: Constants.BASE_URL,
      url: Constants.LIST_ORDER_BY_SALE,
      method: 'POST',
      headers: this.state.token
    });

    let val = res.data.data.result;

    this.pagination(val);
    this.setState({ dataApi: res.data.data.result, arrName: res.data.data.company, arrPackage: res.data.data.package });

    let active = 0

    this.setState({ isLoading: false, totalActive: active });
  }

  searchKey() {
    const { indexPage, key, keyEmail, keyCompany, keyPhone, keyFax, keyAddress,
      keyWebsite, keyCode, keyDateCreate, keyStatus } = this.state;
    // this.setState({ key: key })

    if (key != '' || keyStatus != '') {
      let d = []
      this.state.dataApi.map(val => {
        if (val.Status.toLocaleUpperCase().includes(keyStatus.toLocaleUpperCase())) {

          d.push(val)
        }
      })
      let active = 0

      d.map(val => {
        if (val.Status == "Actived") {
          active = active + 1
        }
      })

      this.setState({ data: d, totalActive: active })
    } else {
      let active = 0

      this.state.dataApi.map(val => {
        if (val.Status == "Actived") {
          active = active + 1
        }
      })

      this.setState({ data: this.state.arrPagination[indexPage], totalActive: active })
    }
  }

